import clsx from "clsx";
import { Link } from "react-router-dom";
import style from "./SidebarLink.module.scss";

export default function SidebarLink({
  icon,
  text,
  link,
  isActive,
  islist,
  activeNot,
}) {
  return islist ? (
    <Link to={link} className={clsx(style.wrapper2, isActive && style.active)}>
      <div className={style.icon}>
        {isActive ? (
          <img src={icon} alt="nav-icon" />
        ) : (
          <img src={activeNot} alt="nav-icon" />
        )}
      </div>
      <div className={style.text}>{text}</div>
    </Link>
  ) : (
    <Link to={link} className={clsx(style.wrapper, isActive && style.active)}>
      <div className={style.icon}>
        {isActive ? (
          <img src={icon} alt="nav-icon" />
        ) : (
          <img src={activeNot} alt="nav-icon" />
        )}
      </div>
      <div className={style.text}>{text}</div>
    </Link>
  );
}
