import { Divider } from "@material-ui/core";
import Badge from "@material-ui/core/Badge";
import Drawer from "@material-ui/core/Drawer";
import MenuItem from "@material-ui/core/MenuItem";
import Popover from "@material-ui/core/Popover";
import LogoImg from "assets/images/main/02.png.svg";
import Bell from "assets/images/main/ic_bell.png";
import clsx from "clsx";
import InlineLoader from "components/shared/InlineLoader";
import { useSidebarContext, useUserContext } from "contexts/AllContexts";
import * as React from "react";
import { useState } from "react";
import { Menu, X } from "react-feather";
import { useNavigate } from "react-router-dom";
import { getRequest } from "utils/api";
import style from "./DashNav.module.scss";

export default function DashNav() {
  const SidebarContext = useSidebarContext();
  const UserContext = useUserContext();

  const sidebarIsOpen = SidebarContext.state.isOpen;
  const sidebarIsMobile = SidebarContext.state.isMobile;
  const profile = UserContext.state.profile;

  const navigate = useNavigate();

  const [notifications, setNotifications] = useState([]);
  const [notiItems, setNotiItems] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  function toggleSidebar() {
    SidebarContext.dispatch({ type: "TOGGLE_SIDEBAR" });
  }

  function handleClickLogo() {
    navigate("/");
  }

  function fetchNotifications() {
    setIsLoading(true);
    getRequest("/notifications")
      .then((resp) => {
        const items = [];

        for (const noti of resp.data) {
          items.push(
            <div style={{ padding: "10px" }}>
              <h3
                style={{
                  fontSize: "17px",
                  fontWeight: "500",
                  marginBottom: "10px",
                }}
              >
                {noti.title}
              </h3>
              <p style={{ fontSize: "14px" }}>{noti.body}</p>
            </div>
          );
          items.push(<Divider />);
        }

        items.pop();

        setNotiItems(items);
        setNotifications(resp.data);
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => setIsLoading(false));
  }

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handlelog = () => {
    setAnchorEl(null);

    localStorage.removeItem("accessToken");
    sessionStorage.removeItem("accessToken");
    window.location.href = "/auth/login";
  };
  const [state, setState] = React.useState(false);

  const toggleDrawer = (open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState(open);
  };

  React.useEffect(() => {
    fetchNotifications();
  }, []);

  return (
    <div className={clsx(style.wrapper)}>
      <div className={style.logo} onClick={handleClickLogo}>
        <img src={LogoImg} alt="" />
      </div>

      <button
        className={clsx(style.ham, sidebarIsMobile && style.mobile)}
        onClick={toggleSidebar}
      >
        {sidebarIsOpen ? <X size={15} /> : <Menu size={15} />}
      </button>

      <div className={style.box}>
        <Badge
          overlap="rectangular"
          badgeContent={notifications?.length || 0}
          color="primary"
        >
          <button className={style.button} onClick={toggleDrawer(true)}>
            <div className={style.bell}>
              <img src={Bell} alt="" />
            </div>
          </button>
        </Badge>

        <div onClick={handleMenu} className={style.name}>
          Hello,<span>{profile.fullname}</span>
          <div className={style.imgWrap}>
            <img src={profile.photo} alt="" />
          </div>
        </div>

        <Popover
          id="menu-appbar"
          anchorEl={anchorEl}
          keepMounted
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "center",
          }}
          open={open}
          onClose={handleClose}
        >
          <MenuItem
            style={{
              padding: "18px",
              fontFamily: "Outfit",
              fontSize: "13px",
              borderBottom: "1px solid #E5E5E5",
            }}
            onClick={() => {
              navigate("/dashboard/candidate/settings?tabKey=change_password");
              handleClose();
            }}
          >
            Change Password
          </MenuItem>
          <MenuItem
            style={{
              padding: "18px",
              fontFamily: "Outfit",
              fontSize: "13px",
              borderRadius: "10px",
            }}
            onClick={handlelog}
          >
            Logout
          </MenuItem>
        </Popover>
      </div>

      <div className={style.boxMobile}>
        <Badge
          overlap="rectangular"
          badgeContent={notifications?.length || 0}
          color="primary"
        >
          <button className={style.button} onClick={toggleDrawer(true)}>
            <div className={style.bell}>
              <img src={Bell} alt="" />
            </div>
          </button>
        </Badge>

        <div onClick={handleMenu} className={style.name}>
          <div className={style.imgWrap}>
            <img src={profile.photo} alt="" />
          </div>
        </div>
      </div>

      <React.Fragment>
        <Drawer anchor={"right"} open={state} onClose={toggleDrawer(false)}>
          <ul
            style={{
              width: "24vw",
              minWidth: "300px",
              padding: "15px",
              display: "flex",
              flexDirection: "column",
              gap: "15px",
              listStyle: "none",
            }}
          >
            {isLoading ? <InlineLoader /> : notiItems}
          </ul>
        </Drawer>
      </React.Fragment>
    </div>
  );
}
