import Popover from "@material-ui/core/Popover";
import { Field, Formik } from "formik";
import { useEffect, useRef, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import { getRequest, patchRequest, postRequest } from "utils/api";
import style from "./EditLicense.module.scss";

const options = ["RISE", "SAIL", "REDESIGN"];

const EditLicense = () => {
  const { licenseId } = useParams();
  const navigateTo = useNavigate();

  const ref = useRef(null);

  const [isLoading, setIsLoading] = useState(false);
  const [selectedLicense, setSelectedLicense] = useState();
  const [formData, setFormData] = useState({
    first_name: "",
    last_name: "",
    email: "",
    category: "RISE",
    group: "",
  });
  const [licenseGroups, setLicenseGroups] = useState([]);
  const [groupText, setGroupText] = useState("");

  const matchingGroups = licenseGroups.filter((item) =>
    item.name.includes(groupText)
  );

  const licenceGroupOptions =
    groupText.length > 0 ? matchingGroups : licenseGroups;

  function resetForm() {
    setFormData({
      first_name: "",
      last_name: "",
      email: "",
      category: "RISE",
      group: "",
    });
    setGroupText("");
  }

  async function fetchLicense() {
    if (!licenseId) {
      toast.error("No license is selected!");
    }

    try {
      const res = await getRequest(`/vendors/licences/${licenseId}`);

      const { first_name, last_name, email, category, group } = res.data;

      setSelectedLicense(res.data);
      setFormData({ first_name, last_name, email, category, group });
    } catch {}
  }

  async function fetchLicenseGroups() {
    try {
      const res = await getRequest("/vendors/licence_groups/");

      setLicenseGroups(res.data.map((row) => ({ id: row.id, name: row.name })));
    } catch {
    } finally {
    }
  }

  function isValid() {
    const { first_name, last_name, email, group } = formData;

    if (!licenseId || selectedLicense === null) {
      return false;
    }

    if (!first_name || !last_name || !email || !group) {
      toast.error("Please enter all details!");
      return false;
    }

    return true;
  }

  async function handleSubmit() {
    if (!isValid()) {
      return;
    }

    const { first_name, last_name, email, group } = formData;

    setIsLoading(true);
    try {
      await patchRequest(`/vendors/licences/${licenseId}`, {
        first_name,
        last_name,
        email,
        group,
      });

      toast.success("License Updated Successfully!");
      navigateTo("/dashboard/vendor/AssignedLicenses");
    } catch (e) {
      toast.error("Something went wrong! Please try again.");
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  }

  function handleChange(e) {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  }

  async function handleSelectGroup(value, isCreating = false) {
    if (isCreating) {
      await createLicenseGroup(value);
      return;
    }

    const group = licenseGroups.find((item) => item.id === value);

    setFormData((prev) => ({ ...prev, group: value }));
    setGroupText(group.name);
    handleClose();
  }

  async function createLicenseGroup(name) {
    try {
      await postRequest("/vendors/licence_groups/", { name });
      fetchLicenseGroups();
    } catch {}
  }

  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  useEffect(() => {
    fetchLicenseGroups();
    fetchLicense();
  }, [licenseId]);

  useEffect(() => {
    if (selectedLicense?.group) handleSelectGroup(selectedLicense.group);
  }, [licenseGroups, selectedLicense]);

  return (
    // <Container>
    <div className={style.main}>
      <div className={style.head}>
        <div>
          <p className={style.title}>Edit License</p>
        </div>
        <div>
          {/* <p className={style.subtitle}>
            Once a license is assigned, its specifics cannot be altered or
            retrieved; please proceed with caution.
          </p> */}
        </div>
      </div>
      <Formik initialValues={formData} onSubmit={handleSubmit}>
        {({ handleSubmit }) => (
          <form onChange={handleChange} onSubmit={handleSubmit}>
            <div className={style.div1}>
              <div className={style.subDiv}>
                <label className={style.label}>First Name</label>
                <Field
                  type="text"
                  name="first_name"
                  className={style.inputfld}
                  value={formData.first_name}
                  disabled={!selectedLicense || isLoading}
                />
              </div>
              <div className={style.subDiv}>
                <label className={style.label}>Last Name</label>
                <Field
                  type="text"
                  name="last_name"
                  className={style.inputfld}
                  value={formData.last_name}
                  disabled={!selectedLicense || isLoading}
                />
              </div>
            </div>
            <div className={style.div1}>
              <div className={style.subDiv}>
                <label className={style.label}>Email Address</label>
                <Field
                  type="email"
                  name="email"
                  className={style.inputfld}
                  value={formData.email}
                  disabled={!selectedLicense || isLoading}
                />
              </div>
              <div className={style.subDiv}>
                <div className={style.groupNameContainer}>
                  <label className={style.label}>Select Category </label>
                  <Field
                    as="select"
                    name="category"
                    className={style.inputfld}
                    value={formData.category}
                    disabled
                  >
                    {options.map((option) => (
                      <option value={option}>{option}</option>
                    ))}
                  </Field>
                  <svg
                    width="14"
                    height="8"
                    viewBox="0 0 14 8"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M1 0.999999L6.76562 6.76562L12.5312 1"
                      stroke="#D3D6E4"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                </div>
              </div>
            </div>
            <div className={style.div2}>
              <div className={style.subDiv}>
                <label className={style.label}>Group Name</label>

                <div className={style.groupNameContainer} onClick={handleClick}>
                  <input
                    type="text"
                    className={style.inputfld}
                    onChange={(e) => setGroupText(e.target.value)}
                    value={groupText}
                    placeholder="Type existing group or create new"
                    list="cars"
                    ref={ref}
                  />
                  <svg
                    width="14"
                    height="8"
                    viewBox="0 0 14 8"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M1 0.999999L6.76562 6.76562L12.5312 1"
                      stroke="#D3D6E4"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                </div>
                <Popover
                  id={id}
                  open={open}
                  anchorEl={anchorEl}
                  onClose={() => handleClose()}
                  keepMounted
                  anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "center",
                  }}
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "center",
                  }}
                >
                  <div style={{ maxHeight: "100px", overflow: "auto" }}>
                    {licenceGroupOptions.map((item) => (
                      <div
                        className={style.option}
                        style={{
                          paddingLeft: "1em",
                          paddingRight: "19em",
                          paddingBottom: "0.1em",
                          paddingTop: "0.1em",
                          cursor: "pointer",
                        }}
                        onClick={() => handleSelectGroup(item.id)}
                      >
                        {item.name}
                      </div>
                    ))}
                    {groupText.length > 0 && matchingGroups.length === 0 && (
                      <div
                        className={style.option}
                        style={{
                          paddingLeft: "1em",
                          paddingRight: "19em",
                          paddingBottom: "0.1em",
                          paddingTop: "0.1em",
                          cursor: "pointer",
                        }}
                        onClick={(e) => handleSelectGroup(groupText, true)}
                      >
                        {groupText} - Create New
                      </div>
                    )}
                  </div>
                </Popover>
              </div>
              <div className={style.buttonWrapper}>
                <button className={style.button} disabled={isLoading}>
                  <p className={style.buttontxt}>Submit</p>
                </button>
              </div>
            </div>
          </form>
        )}
      </Formik>
      <div></div>
    </div>
    // </Container>
  );
};

export default EditLicense;
