import LogoImg from "assets/images/main/02.png.svg";
import clsx from "clsx";
import Modal from "components/shared/Modal";
import { useSidebarContext } from "contexts/AllContexts";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Crown from "../../assets/images/dashboard/upgrade.svg";
import style from "./Sidebar.module.scss";
import SidebarLinks from "./SidebarLinks";
import Popup from "./UpgradeModals/Popup";
import { X } from "react-feather";

export default function Sidebar() {
  const SidebarContext = useSidebarContext();
  const [isModal, setModal] = useState(false);

  const isOpenSidebar = SidebarContext.state.isOpen;
  const isMobileSidebar = SidebarContext.state.isMobile;

  function toggleSidebar() {
    SidebarContext.dispatch({ type: "TOGGLE_SIDEBAR" });
  }

  function closeSidebar() {
    SidebarContext.dispatch({ type: "CLOSE_SIDEBAR" });
  }

  const navigate = useNavigate();

  function handleClickLogo() {
    navigate("/");
  }

  const handleOpen = () => {
    setModal(true);
  };
  const handleCloseModal = () => {
    setModal(false);
  };

  return (
    <div
      className={clsx(style.wrapper, isOpenSidebar ? style.open : style.closed)}
    >
      {isMobileSidebar && (
        <div className={clsx(style.logo)}>
          <img src={LogoImg} alt="" onClick={handleClickLogo} />

          <X size={25} onClick={closeSidebar} />
        </div>
      )}

      <div className={style.links}>
        <SidebarLinks
          isMobileSidebar={isMobileSidebar}
          toggleSidebar={toggleSidebar}
        />
      </div>
      <div className={style.upgradeWrapper}>
        <div className={style.upgrade} onClick={handleOpen}>
          <img src={Crown} />
        </div>
        <p className={style.p}>CareerTest.ai </p>
        <p className={style.p2}>© 2024 All Rights Reserved</p>
      </div>
      <Modal isOpen={isModal} contentClassName={style.modal}>
        <Popup handleCloseModal={handleCloseModal} />
      </Modal>
    </div>
  );
}
