import Collapse from "@material-ui/core/Collapse";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import { makeStyles } from "@material-ui/core/styles";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import ic_analytics from "assets/images/dashboard/ic_analytics.svg";
import ic_analyticsNot from "assets/images/dashboard/ic_analytics_not_active.svg";
import ic_customer from "assets/images/dashboard/ic_customer.svg";
import ic_customerNot from "assets/images/dashboard/ic_customer_not_active.svg";
import ic_dashboard from "assets/images/dashboard/ic_dashboard.svg";
import ic_dashboardNot from "assets/images/dashboard/ic_dashboard_not_active.svg";
import ic_menu from "assets/images/dashboard/ic_menus.svg";
import ic_menuNot from "assets/images/dashboard/ic_menus_not_active.svg";
import ic_Order from "assets/images/dashboard/ic_order.svg";
import ic_OrderNot from "assets/images/dashboard/ic_order_not_active.svg";
import { useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import SidebarLink from "./SidebarLink";
import style from "./SidebarLinks.module.scss";

const _LINKS = [
  {
    text: "Overview",
    link: "/dashboard/vendor/home",
    icon: ic_dashboard,
    notactive: ic_dashboardNot,
  },
  {
    text: "Licenses",
    link: "/dashboard/vendor/AssignedLicenses",
    link1: "/dashboard/vendor/AssignBulk",
    // link2: "/dashboard/vendor/AssignedLicenses",
    icon: ic_customer,
    notactive: ic_customerNot,
  },
  {
    text: "Organization Settings",
    link: "/dashboard/vendor/OrganizationSettings",
    icon: ic_menu,
    notactive: ic_menuNot,
  },
  {
    text: "Customize Landing Page",
    link: "/dashboard/vendor/CustomizeLanding",
    icon: ic_Order,
    notactive: ic_OrderNot,
  },
  {
    text: "My Orders",
    link: "/dashboard/vendor/orders",
    icon: ic_analytics,
    notactive: ic_analyticsNot,
  },
];
const SUBLINKS = [
  { text: "Assigned Licenses", link: "/dashboard/vendor/AssignedLicenses" },
  { text: "Unassigned Licenses", link: "/dashboard/vendor/UnassignedLicenses" },
  // { text: "Assign New", link: "/dashboard/vendor/AssignNew" },
  { text: "Assign Bulk", link: "/dashboard/vendor/AssignBulk" },
];
const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: "52px",
  },
}));

export default function SidebarLinks() {
  const location = useLocation();
  const classes = useStyles();
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);

  const handleClick = (link) => {
    setOpen(!open);
    navigate(link);
  };

  function isActive(link) {
    if (
      link === location.pathname ||
      (link == "/dashboard/vendor/home" &&
        location.pathname == "/dashboard/vendor")
    )
      return true;
    return false;
  }

  return (
    <>
      <div className={style.wrapper}>
        {_LINKS.map((link) =>
          link.text == "Licenses" ? (
            <List>
              {isActive(link.link) ||
                isActive(link.link1) ||
                isActive(link.link2) ? (
                <div className={style.mainlist1}>
                  <div className={style.border}></div>
                  <ListItem button onClick={() => handleClick(link.link)} className={style.list1}>
                    <div>
                      <img src={link.icon} />
                      <span>Licenses</span>
                    </div>
                    {open ? <ExpandLess /> : <ExpandMore />}
                  </ListItem>
                </div>
              ) : (
                <div className={style.mainlist}>
                  <div className={style.border}></div>
                  <ListItem
                    button
                    onClick={() => handleClick(link.link)}
                    className={style.list}
                  >
                    <div>
                      <img src={link.icon} />
                      <span>Licenses</span>
                    </div>
                    {open ? <ExpandLess /> : <ExpandMore />}
                  </ListItem>
                </div>
              )}
              {SUBLINKS.map((link) => (
                <Collapse in={open} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    <ListItem
                      button
                      className={style.nested}
                      onClick={() => navigate(link.link)}
                    >

                      <ListItemText primary={link.text} />
                    </ListItem>
                  </List>
                </Collapse>
              ))}
            </List>
          ) : (
            <SidebarLink
              isActive={isActive(link.link)}
              key={link.text}
              text={link.text}
              link={link.link}
              icon={link.icon}
              activeNot={link.notactive}
            />
          ),
        )}
      </div>
    </>
  );
}
