import style from "./InterestedArea.module.scss";
import Locked from "./Locked";

const InterestedArea = ({
  isAssessmentCompleted,
  hasActivePlan,
  notCompleted,
  report,
}) => {
  const interestedAreas = report?.dominant_interest || [];

  return (
    <div>
      {hasActivePlan ? (
        isAssessmentCompleted ? (
          <div className={style.card}>
            {interestedAreas.slice(0, 6).map((item, index) => (
              <div className={style.innercard} key={index}>
                {item[0]}
                <div>
                  <div className={style.icon}>
                    <img src={item[1]} alt="" width={40} />
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          notCompleted
        )
      ) : (
        <Locked />
      )}
    </div>
  );
};

export default InterestedArea;
