import clsx from "clsx";
import PlanPrompt from "components/assessment/PlanPrompt";
import InlineLoader from "components/shared/InlineLoader";
import Modal from "components/shared/Modal";
import { useUserContext } from "contexts/AllContexts";
import { forwardRef, useEffect, useState } from "react";
import { Download, Eye } from "react-feather";
import { getRequest } from "utils/api";
import Popup from "../UpgradeModals/Popup";
import BarGraph from "./BarGraph";
import style from "./MyReports.module.scss";
import PrintReport from "./full-report/PrintReport";
import { _Colors } from "./full-report/common/helper";
import { useNavigate } from "react-router-dom";

function MyReports(props, ref) {
  const navigateTo = useNavigate();

  const MainTabs = [
    { key: "view", name: "View Full Report", icon: <Eye /> },
    { key: "download", name: "Download Full Report", icon: <Download /> },
  ];

  const SectionTabs = [
    { key: "personality_traits", name: "Personality Traits" },
    { key: "aptitude", name: "Aptitude" },
    { key: "interest_area", name: "Interest Area" },
    { key: "wvp", name: "Work Value Preferense" },
    { key: "eq", name: "EQ" },
  ];

  const [isLoading, setIsLoading] = useState(false);
  const [isAssessmentCompleted, setIsAssessmentCompleted] = useState(false);
  const [isPersonalityCompleted, setIsPersonalityCompleted] = useState(false);
  const [mainTab, setMainTab] = useState(MainTabs[0]);

  const [showModal, setShowModal] = useState(false);

  const [activeTab, setActiveTab] = useState(SectionTabs[0]);

  function handleClickTab(tabKey) {
    const selectedTab = SectionTabs.find((item) => item.key === tabKey);

    setActiveTab(selectedTab);
  }

  function handleClickMainTab(tabKey) {
    if (tabKey === "download" && !hasActivePlan) {
      setShowModal(true);
      return;
    }

    const selectedTab = MainTabs.find((item) => item.key === tabKey);

    setMainTab(selectedTab);
  }

  const { state, hasActivePlan = false } = useUserContext();
  const [report, setReport] = useState();

  function navigateToAssessment() {
    navigateTo("/assessment/live");
  }

  async function getReport() {
    setIsLoading(true);

    try {
      const res = await getRequest(`/assessment/status/update`);

      const { is_personality_finished, is_assessment_finished } = res.data;
      setIsPersonalityCompleted(is_personality_finished);

      if (!hasActivePlan) {
        const response = await getRequest(`/assessment/basic/report`);

        setActiveTab(SectionTabs[0]);

        setReport(response.data);
        setIsAssessmentCompleted(false);
      } else if (is_assessment_finished) {
        const response = await getRequest(`/assessment/report`);

        setReport(response.data);
        setIsAssessmentCompleted(true);
      } else {
        setIsAssessmentCompleted(false);
        setIsLoading(false);
      }
    } catch (err) {
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  }

  const AssessmentNotCompleted = () => (
    <div className={style.notCompleteWrapper}>
      <div className={style.title}>Complete Assessment</div>
      <div>
        Oops, you have not completed the assessment. Please take the assessment
        to view report
      </div>
      <button onClick={navigateToAssessment}>Take Assessment</button>
    </div>
  );

  const tabViews = {
    personality_traits:
      isPersonalityCompleted || isAssessmentCompleted ? (
        <div className={style.row}>
          <div className={clsx(style.graph, style.personality)}>
            {report?.graph_data && (
              <BarGraph
                height={400}
                horizontal={true}
                title={"Personality"}
                data={{
                  labels: report?.graph_data?.personality_labels,
                  values: report?.graph_data?.personality_final_list,
                }}
                color={_Colors.personality}
              />
            )}
          </div>
        </div>
      ) : (
        <AssessmentNotCompleted />
      ),
    aptitude: hasActivePlan ? (
      isAssessmentCompleted ? (
        <div className={style.row}>
          <div className={clsx(style.graph, style.aptitude)}>
            {report?.graph_data && (
              <BarGraph
                height={300}
                horizontal={true}
                title={"Aptitude"}
                data={{
                  labels: report?.graph_data?.aptitude_labels,
                  values: report?.graph_data?.aptitude_final_list,
                }}
                color={_Colors.aptitude}
              />
            )}
          </div>
        </div>
      ) : (
        <AssessmentNotCompleted />
      )
    ) : (
      <PlanPrompt />
    ),
    interest_area: hasActivePlan ? (
      isAssessmentCompleted ? (
        <div className={style.row}>
          <div className={clsx(style.graph, style.career)}>
            {report?.graph_data && (
              <BarGraph
                height={450}
                horizontal={true}
                title={"Interests"}
                data={{
                  labels: report?.graph_data?.interest_labels,
                  values: report?.graph_data?.interest_final_list,
                }}
                color={_Colors.career}
              />
            )}
          </div>
        </div>
      ) : (
        <AssessmentNotCompleted />
      )
    ) : (
      <PlanPrompt />
    ),
    wvp: hasActivePlan ? (
      isAssessmentCompleted ? (
        <div className={style.row}>
          <div className={clsx(style.graph, style.career)}>
            {report?.graph_data && (
              <BarGraph
                height={300}
                horizontal={true}
                title={"Work Value Preference"}
                data={{
                  labels: report?.graph_data?.wvp_labels,
                  values: report?.graph_data?.wvp_final_list,
                }}
                color={_Colors.wvp}
              />
            )}
          </div>
        </div>
      ) : (
        <AssessmentNotCompleted />
      )
    ) : (
      <PlanPrompt />
    ),
    eq: hasActivePlan ? (
      isAssessmentCompleted ? (
        <div className={style.row}>
          <div className={clsx(style.graph, style.career)}>
            {report?.graph_data && (
              <BarGraph
                height={250}
                horizontal={true}
                title={"Emotional Quotient"}
                data={{
                  labels: report?.graph_data?.eq_labels,
                  values: report?.graph_data?.eq_final_list,
                }}
                color={_Colors.eq}
              />
            )}
          </div>
        </div>
      ) : (
        <AssessmentNotCompleted />
      )
    ) : (
      <PlanPrompt />
    ),
  };

  useEffect(() => {
    getReport();
  }, []);

  return (
    <div ref={ref} {...props} className={style.wrapper}>
      <>
        <div className={style.header}>
          <div className={style.maintab}>
            {MainTabs.map((tab) => (
              <div
                className={clsx(
                  style.tab,
                  tab.key === mainTab.key && style.open
                )}
                onClick={() => handleClickMainTab(tab.key)}
                key={tab.key}
              >
                {tab.name}
                {tab.icon}
              </div>
            ))}
          </div>
        </div>
        {mainTab.key === "view" && (
          <div className={style.tabmain}>
            <div className={style.tabs}>
              {SectionTabs.map((tab) => (
                <div
                  className={clsx(
                    style.tab,
                    tab.key === activeTab.key && style.open
                  )}
                  onClick={() => handleClickTab(tab.key)}
                  key={tab.name}
                >
                  {tab.name}
                </div>
              ))}
            </div>

            <div className={style.section}>
              {isLoading ? (
                <div className={style.loaderWrapper}>
                  <InlineLoader size={90} />
                </div>
              ) : (
                tabViews[activeTab.key]
              )}
            </div>
          </div>
        )}

        {mainTab.key === "download" && <PrintReport />}
      </>

      <Modal isOpen={showModal} contentClassName={style.modal}>
        <Popup handleCloseModal={() => setShowModal(false)} />
      </Modal>
    </div>
  );
}

export default forwardRef(MyReports);
