import Chart from "react-apexcharts";

export default function Chart3({ assigned, completed }) {
  const state = {
    series: [
      {
        name: "Assigned",
        data: assigned,
      },
      {
        name: "Completed",
        data: completed,
      },
    ],
    options: {
      colors: ["#3D92ED", "#3D3D3D"],
      legend: {
        position: "top",
        horizontalAlign: "right",
      },
      chart: {
        type: "bar",
        height: 350,
        toolbar: {
          show: false,
        },
      },
      plotOptions: {
        bar: {
          horizontal: false,
          columnWidth: "55%",
          endingShape: "rounded",
        },
      },
      dataLabels: {
        enabled: false,
      },
      stroke: {
        show: true,
        width: 2,
        colors: ["transparent"],
      },
      xaxis: {
        categories: ["Rise", "Sail", "Redesign"],
      },
      yaxis: {},
      fill: {
        opacity: 1,
      },
      tooltip: {
        y: {
          formatter: function (val) {
            return val;
          },
        },
      },
    },
  };

  return (
    <div>
      <Chart
        options={state.options}
        series={state.series}
        type="bar"
        height={399}
      />
    </div>
  );
}
