import style from "./PersonalTraits.module.scss";

function PersonalityTraits({
  isAssessmentCompleted,
  isPersonalityCompleted,
  report,
  notCompleted,
}) {
  const personalityTraits = report?.dominant_personality || [];

  return (
    <div>
      {isAssessmentCompleted || isPersonalityCompleted ? (
        <div className={style.card}>
          {personalityTraits.slice(0, 6).map((item, index) => (
            <div className={style.innercard} key={index}>
              {item[0]}
              <div>
                <div className={style.icon}>
                  <img src={item[1]} alt="" width={40} />
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        notCompleted
      )}
    </div>
  );
}

export default PersonalityTraits;
