import CareerTestLogo from "assets/images/main/02.png.svg";
import InlineLoader from "components/shared/InlineLoader";
import { useUserContext } from "contexts/AllContexts";
import { Field, Formik } from "formik";
import { useEffect, useState } from "react";
import ImageUploading from "react-images-uploading";
import { toast } from "react-toastify";
import { getRequest, patchRequest } from "utils/api";
import style from "./OrganizationSettings.module.scss";

export default function Accountsettings() {
  const [formData, setFormData] = useState({
    name: "",
    type: "",
    url: "",
    email: "",
    country: "",
    city: "",
    contact_person: "",
    bio: "",
    slug: "",
  });

  const [isLoading, setIsLoading] = useState(false);
  const [imgUploadLoading, setImgUploadLoading] = useState(false);
  const [types, setTypes] = useState([]);

  const { vendorProfile, getProfile } = useUserContext();

  const [isEdit, setIsEdit] = useState(false);

  async function onImageChange(imageList) {
    setImgUploadLoading(true);
    const image = imageList[0].file;
    const formData = new FormData();
    formData.append("photo", image);

    setImgUploadLoading(true);
    try {
      await patchRequest("/vendors/page/build/", formData);

      toast.success("Image Upload Success!");
      getProfile();
    } catch (e) {
      console.error(e);
      toast.error("Something went wrong!");
    } finally {
      setImgUploadLoading(false);
    }
  }

  async function handleDeletePhoto(e) {
    e.preventDefault();

    const formData = new FormData();
    formData.append("photo", null);

    try {
      await patchRequest("/vendors/page/build/", { photo: null });

      toast.success("Image Removed!");
      getProfile();
    } catch (e) {
      console.error(e);
      toast.error("Something went wrong!");
    } finally {
    }
  }

  function handleFormChange(e) {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  }

  async function handleSubmit() {
    const { name, type } = formData;

    if (!name || !type) {
      toast.error("Please enter the organization name and type!");
      return;
    }

    setIsLoading(true);

    try {
      const { name: fullname, ...params } = formData;
      await patchRequest("/vendors/page/build/", {
        fullname,
        ...params,
      });

      toast.success("Update success!");
      getProfile();
      setIsEdit(false);
    } catch (e) {
      toast.error("Something went wrong! Please try again.");
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  }

  async function fetchTypes() {
    try {
      const res = await getRequest("/vendors/types");

      setTypes(
        res.data.map((item) => ({
          label: item[0],
          value: item[1],
        }))
      );
    } catch {}
  }

  useEffect(() => {
    fetchTypes();
  }, []);

  useEffect(() => {
    if (vendorProfile) {
      const {
        fullname: name,
        type,
        url,
        email,
        country,
        city,
        contact_person,
        bio,
        slug,
      } = vendorProfile;
      setFormData({
        name,
        type,
        url,
        email,
        country,
        city,
        contact_person,
        bio,
        slug
      });
    }
  }, [vendorProfile]);

  return (
    <div className={style.wrapper}>
      <Formik initialValues={formData} onSubmit={handleSubmit}>
        {() => (
          <form onChange={handleFormChange} className={style.emailBox}>
            <p className={style.head}>Account Settings</p>
            <p className={style.brief}>
              If you encounter any difficulties, please reach out to the
              CareerTest.ai team at <span>hello@careertest.ai</span>
            </p>
            <div className={style.profilesection}>
              <div className={style.profilephoto}>
                <img
                  src={vendorProfile?.photo || CareerTestLogo}
                  alt="Organization"
                />
              </div>
              <div className={style.profilebuttons}>
                <div className={style.changepicture}>
                  <ImageUploading
                    onChange={onImageChange}
                    dataURLKey="data_url"
                  >
                    {({ onImageUpload }) => (
                      <div
                        className={style.changepicture}
                        onClick={(e) => {
                          e.preventDefault();
                          onImageUpload(e);
                        }}
                        disabled={imgUploadLoading}
                      >
                        <button disabled={imgUploadLoading}>
                          {imgUploadLoading ? (
                            <InlineLoader size={20} />
                          ) : (
                            "Change Picture"
                          )}
                        </button>
                      </div>
                    )}
                  </ImageUploading>
                </div>
                <div className={style.deletepicture}>
                  <button
                    onClick={handleDeletePhoto}
                    disabled={imgUploadLoading || !vendorProfile?.photo}
                  >
                    Delete Picture
                  </button>
                </div>
              </div>
            </div>

            <div className={style.textFieldGroup}>
              <div className={style.row}>
                <div className={style.column}>
                  <div className={style.field}>
                    <label htmlFor="first_name">Organization Name</label>
                    <Field
                      className={style.textField}
                      type="text"
                      name="name"
                      placeholder="Oxford University"
                      disabled={!isEdit}
                      value={formData.name}
                    />
                  </div>
                </div>
                <div className={style.column}>
                  <div className={style.field}>
                    <label htmlFor="type">Organization Type</label>
                    <Field
                      as="select"
                      name="type"
                      className={style.select}
                      value={formData.type}
                      disabled={!isEdit}
                    >
                      {types.map((type) => (
                        <option value={type.label}>{type.label}</option>
                      ))}
                    </Field>
                  </div>
                </div>
              </div>
              <div className={style.row}>
                <div className={style.column}>
                  <div className={style.field}>
                    <label htmlFor="url">Organization URL</label>
                    <Field
                      className={style.textField}
                      type="text"
                      name="url"
                      placeholder={`careertest.ai/${formData.slug || "oxford"}/login`}
                      disabled
                      value={formData.url}
                    />
                  </div>
                </div>
                <div className={style.column}>
                  <div className={style.field}>
                    <label htmlFor="last_name">Email ID</label>
                    <Field
                      className={style.textField}
                      type="email"
                      name="email"
                      placeholder="contact@oxford.org"
                      disabled={!isEdit}
                      value={formData.email}
                    />
                  </div>
                </div>
              </div>
              <div className={style.row}>
                <div className={style.column}>
                  <div className={style.field}>
                    <label htmlFor="country">Country</label>
                    <Field
                      className={style.textField}
                      type="text"
                      name="country"
                      placeholder="United Kingdom"
                      disabled={!isEdit}
                      value={formData.country}
                    />
                  </div>
                </div>
                <div className={style.column}>
                  <div className={style.field}>
                    <label htmlFor="city">City/ Place</label>
                    <Field
                      className={style.textField}
                      type="text"
                      name="city"
                      placeholder="Oxford, England"
                      disabled={!isEdit}
                      value={formData.city}
                    />
                  </div>
                </div>
              </div>
              <div className={style.row}>
                <div className={style.column}>
                  <div className={style.field}>
                    <label htmlFor="contact_person">Contact Person Name</label>
                    <Field
                      className={style.textField}
                      type="text"
                      name="contact_person"
                      placeholder="John Doe"
                      disabled={!isEdit}
                      value={formData.contact_person}
                    />
                  </div>
                </div>
              </div>
              <div className={style.field}>
                <label htmlFor="bio">About Organization</label>
                <Field
                  className={style.textarea}
                  as="textarea"
                  name="bio"
                  placeholder="Write your bio here..."
                  disabled={!isEdit}
                  value={formData.bio}
                />
                <div className={style.wordCounter}>
                  {formData.bio?.length || 0}/200 words
                </div>
              </div>
              {/* <div className={style.row}>
            <div className={style.column}>
              <div className={style.field}>
                <label htmlFor="first_name">New Password </label>
                <input
                  className={style.textField}
                  type="password"
                  name="first_name"
                  placeholder="First Name"
                  disabled={!isEdit}
                />
              </div>
            </div>
            <div className={style.column}>
              <div className={style.field}>
                <label htmlFor="last_name">Confirm Password </label>
                <input
                  className={style.textField}
                  type="password"
                  name="last_name"
                  placeholder="Oxford, England"
                  disabled={!isEdit}
                />
              </div>
            </div>
            </div> */}
            </div>
            <div className={style.btnContainer}>
              {isEdit ? (
                <>
                  <button
                    className={style.submitBtn}
                    disabled={isLoading}
                    onClick={(e) => {
                      e.preventDefault();
                      setIsEdit(false);
                    }}
                  >
                    Cancel
                  </button>
                  <button
                    className={style.submitBtn}
                    disabled={isLoading}
                    onClick={() => handleSubmit()}
                  >
                    {isLoading ? <InlineLoader size={20} /> : "Submit"}
                  </button>
                </>
              ) : (
                <button
                  className={style.submitBtn}
                  onClick={(e) => {
                    e.preventDefault();
                    setIsEdit(true);
                  }}
                >
                  Edit
                </button>
              )}
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
}
