import style from "./Aptitude.module.scss";
import Locked from "./Locked";

function Aptitudes({
  isAssessmentCompleted,
  hasActivePlan,
  notCompleted,
  report,
}) {
  const aptitudes = report?.dominant_aptitude || [];

  return (
    <div>
      {hasActivePlan ? (
        isAssessmentCompleted ? (
          <div className={style.card}>
            {aptitudes.slice(0, 6).map((item, index) => (
              <div className={style.innercard} key={index}>
                {item[0]}
                <div>
                  <div className={style.icon}>
                    <img src={item[1]} alt="" width={40} />
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          notCompleted
        )
      ) : (
        <Locked />
      )}
    </div>
  );
}

export default Aptitudes;
