import FormControl from "@material-ui/core/FormControl";
import InputBase from "@material-ui/core/InputBase";
import Select from "@material-ui/core/Select";
import { makeStyles } from "@material-ui/core/styles";
import SearchIcon from "@material-ui/icons/Search";
import Pagination from "@material-ui/lab/Pagination";
import InlineLoader from "components/shared/InlineLoader";
import moment from "moment";
import * as React from "react";
import { useEffect, useState } from "react";
import { getRequest } from "utils/api";
import style from "./Orders.module.scss";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  root: {
    "& > *": {
      marginTop: theme.spacing(2),
    },
  },
}));

export default function Orders() {
  const classes = useStyles();
  const [state, setState] = React.useState("");
  const [orders, setOrders] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const [search, setSearch] = useState("");
  const [limit, setLimit] = useState(10);
  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);

  const offset = (page - 1) * limit;

  function getOrders(limit, offset) {
    setIsLoading(true);

    const queryParams = { limit, offset };

    if (search !== "") {
      queryParams.search = search;
    }

    getRequest(`/vendors/orders`, {}, queryParams)
      .then((resp) => {
        const { count, results } = resp.data;
        setCount(count);
        setOrders(
          results.map((item) => ({
            ...item,
            totalCount:
              parseInt(item.redesign_count) +
              parseInt(item.rise_count) +
              parseInt(item.sail_count),
          }))
        );
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => setIsLoading(false));
  }

  useEffect(() => {
    getOrders(limit, offset);
  }, [limit, offset]);

  return (
    <div className={style.wrapper}>
      <div className={style.tableWrapper}>
        <div className={style.header}>
          Orders
          <div className={style.subheader}>
            View all your license purchase details and transaction history in
            this section.
          </div>
        </div>
        <div className={style.all}>
          <div>
            Show
            <FormControl variant="outlined" className={classes.formControl}>
              <Select
                className={style.suball}
                native
                value={state.age}
                defaultValue="all"
                onChange={(e) => {
                  setLimit(e.target.value);
                }}
                inputProps={{
                  name: "age",
                  id: "outlined-age-native-simple",
                }}
              >
                <option>10</option>
                <option>50</option>
                <option>100</option>
              </Select>
            </FormControl>
          </div>
          <div className={style.search}>
            <InputBase
              onChange={(e) => setSearch(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  getOrders(limit, offset);
                }
              }}
              className={style.input}
              placeholder="Search here"
            />
            <button
              type="submit"
              className={style.iconButton1}
              aria-label="search"
              onClick={() => getOrders(limit, offset)}
            >
              <SearchIcon />
            </button>
          </div>
        </div>
        {isLoading ? (
          <InlineLoader />
        ) : (
          <table>
            <thead>
              <tr>
                <th>Date</th>
                <th>Payment Id</th>
                <th>No. of Licenses</th>
                <th>Amount</th>
                <th>License Type</th>
                <th>Payment Status</th>
              </tr>
            </thead>

            <tbody>
              {orders.map((order, index) => (
                <tr key={order.pid}>
                  <td>{moment(order.created).format("DD/MM/YY")}</td>

                  <td>{order.order_id}</td>
                  <td>{order.totalCount}</td>
                  <td>&#36; {order.amount}</td>
                  <td>
                    {order.sail_count > 0 ? "sail" : ""}{" "}
                    {order.redesign_count > 0 ? "Redesign" : ""}{" "}
                    {order.rise_count > 0 ? "Rise" : ""}{" "}
                  </td>
                  {order.is_paid == false && order.is_processed == false ? (
                    <td> Failed </td>
                  ) : order.is_paid == true && order.is_processed == false ? (
                    <td> Success </td>
                  ) : order.is_paid == false && order.is_processed == true ? (
                    <td> In Progress </td>
                  ) : (
                    ""
                  )}
                </tr>
              ))}
            </tbody>
          </table>
        )}

        <div className={style.last}>
          {/* <div>
            <p className={style.lasttxt}>Showing 1 to 10 of 57 Entries</p>
          </div> */}
          <div className={classes.root}>
            <Pagination
              count={Math.ceil(count / limit)}
              disabled={isLoading}
              page={page}
              value={page}
              onChange={(_, val) => setPage(val)}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
