import { useState } from "react";
import lock from "../../../../assets/images/dashboard/lock.png";
import style from "./Locked.module.scss";
import Modal from "components/shared/Modal";
import Popup from "components/dashboard-customer/UpgradeModals/Popup";

const Locked = () => {
  const [isModal, setModal] = useState(false);

  const handleOpen = () => {
    setModal(true);
  };
  const handleCloseModal = () => {
    setModal(false);
  };

  return (
    <div>
      <div className={style.paddedInvisible}>
        <img src={lock} alt="" className={style.lock} />
        <button className={style.upgradeBtn} onClick={handleOpen}>
          Upgrade Now
        </button>
        <p>
          Experience a “shockingly accurate” understanding of your career
          potential!
        </p>
      </div>

      <Modal isOpen={isModal} contentClassName={style.modal}>
        <Popup handleCloseModal={handleCloseModal} />
      </Modal>
    </div>
  );
};

export default Locked;
