import Chart from "react-apexcharts";

const Chart1 = ({ rise, sail, redesign }) => {
  const state = {
    options: {
      legend: {
        show: false,
      },
      dataLabels: {
        enabled: false,
      },
      labels: ["RISE", "SAIL", "REDESIGN"],
      colors: ["#913DED", "#00D797", "#EDD53D"],
    },
    series: [rise, sail, redesign],
  };

  return (
    <div>
      <Chart
        options={state.options}
        series={state.series}
        type="donut"
        height={200}
      />
    </div>
  );
};

export default Chart1;
