import clsx from "clsx";
import { useSidebarContext } from "contexts/AllContexts";
import { useEffect } from "react";
import { Route, Routes } from "react-router-dom";
import AssignBulk from "./AssignBulk";
import AssignNew from "./AssignNew";
import AssignedLicenses from "./AssignedLicenses";
import ChangePassword from "./ChangePassword";
import style from "./Container.module.scss";
import DashNav from "./DashNav";
import GenLicense from "./GenLicense";
import Home from "./Home";
import Licenses from "./Licenses";
import Orders from "./Orders";
import OrganizationSettings from "./OrganizationSettings";
import Sidebar from "./Sidebar";
import CustomizeLanding from "./customizeLanding/CustomizeLanding";
import Student from "./student-details/Student";
import StudentDetails from "./student-details/StudentDetails";
import UnassignedLicenses from "./UnassignedLicenses";
import EditLicense from "./EditLicense";
import ViewLicense from "./reports/ViewLicense";

export default function Container() {
  const SidebarContext = useSidebarContext();
  const sidebarIsOpen = SidebarContext.state.isOpen;

  function resizeHandler() {
    const width = window.innerWidth;
    if (width < 1150) {
      if (!SidebarContext.state.isMobile) {
        SidebarContext.dispatch({ type: "CLOSE_SIDEBAR" });
      }
      SidebarContext.dispatch({
        type: "SET_IS_MOBILE",
        payload: { isMobile: true },
      });
    } else {
      if (SidebarContext.state.isMobile) {
        SidebarContext.dispatch({ type: "OPEN_SIDEBAR" });
      }
      SidebarContext.dispatch({
        type: "SET_IS_MOBILE",
        payload: { isMobile: false },
      });
    }
  }

  useEffect(() => {
    resizeHandler();
    window.addEventListener("resize", resizeHandler);
  }, []);

  return (
    <div className={style.wrapper}>
      <DashNav />
      <Sidebar />

      <div
        className={clsx(
          style.main,
          sidebarIsOpen ? style.sidebarOpen : style.sidebarClosed
        )}
      >
        <Routes>
          <Route path="/home" element={<Home />} />
          <Route path="/licenses" element={<Licenses />} />
          <Route path="/orders" element={<Orders />} />
          <Route path="/students" element={<StudentDetails />} />
          <Route path="/candidate/:accessKey" element={<Student />} />
          <Route path="/license/new" element={<GenLicense />} />
          <Route
            path="/OrganizationSettings"
            element={<OrganizationSettings />}
          />
          <Route path="/CustomizeLanding" element={<CustomizeLanding />} />
          <Route path="/AssignNew" element={<AssignNew />} />
          <Route path="/AssignBulk" element={<AssignBulk />} />
          <Route path="/AssignedLicenses" element={<AssignedLicenses />} />
          <Route path="/AssignedLicenses/:accessKey" element={<ViewLicense />} />
          <Route path="/AssignedLicenses/:licenseId/edit" element={<EditLicense />} />
          <Route path="/UnassignedLicenses" element={<UnassignedLicenses />} />
          <Route path="/change-password" element={<ChangePassword />} />

          <Route index element={<Home />} />
        </Routes>
      </div>
    </div>
  );
}
