import Button from "components/shared/Button";
import InlineLoader from "components/shared/InlineLoader";
import TextField from "components/shared/formik/TextField";
import { useUserContext } from "contexts/AllContexts";
import { Formik } from "formik";
import { useEffect, useState } from "react";
import ImageUploading from "react-images-uploading";
import { putRequest } from "utils/api";
import style from "./AccountSetting.module.scss";
import { toast } from "react-toastify";

export default function Accountsettings() {
  const [formData, setFormData] = useState({
    first_name: "",
    last_name: "",
    email: "",
    category: "",
  });

  const [isLoading, setIsLoading] = useState(false);
  const [imgUploadLoading, setImgUploadLoading] = useState();

  const userContext = useUserContext();

  const [isEdit, setIsEdit] = useState(false);

  const onChange = (imageList, addUpdateIndex) => {
    setImgUploadLoading(true);
    const image = imageList[0].file;

    const formData = new FormData();
    formData.append("photo", image);

    putRequest(`/accounts/update_photo`, formData)
      .then(() => {
        userContext.getProfile();
        toast.success("Profile Photo Updated...");
      })
      .catch((err) => {
        console.log(err);
        toast.error("Something went wrong! Please try again...");
      })
      .finally(() => {
        setImgUploadLoading(false);
      });
  };

  function toggleIsEdit() {
    setIsEdit((prev) => !prev);
  }

  function changeFormData(e) {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  }

  function handleSubmit(e) {
    e.preventDefault();
    const { first_name, last_name } = formData;

    if (!first_name || !last_name || first_name === "" || last_name === "") {
      toast.error("Please enter the first and last names!");
      return;
    }

    const postData = { first_name, last_name };

    setIsLoading(true);

    putRequest(`/accounts/update_profile`, postData)
      .then(() => {
        setIsEdit(false);
        toast.success("Updated Successfully!");
      })
      .catch((err) => {
        console.error(err.message);
        toast.error("Something went wrong! Please try again...");
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  async function handleDeleteImage(e) {
    e.preventDefault();

    putRequest(`/accounts/update_photo`, { photo: null })
      .then(() => {
        userContext.getProfile();
        toast.success("Profile photo removed...");
      })
      .catch((err) => {
        console.log(err);
        toast.error("Something went wrong! Please try again...");
      })
      .finally(() => {
        setImgUploadLoading(false);
      });
  }

  function handleCancel() {
    setIsEdit(false);
    setInitialFormData();
  }

  function setInitialFormData() {
    const profile = userContext.state.profile;

    setFormData({
      first_name: profile.first_name,
      last_name: profile.last_name,
      email: profile.email,
      category: profile.department,
    });
  }

  useEffect(() => {
    if (userContext.state && userContext.state.profile) setInitialFormData();
  }, [userContext.state]);

  const avatar =
    userContext?.state?.profile?.photo ||
    "https://i1.sndcdn.com/avatars-000812665324-tbg3oh-t500x500.jpg";

  return (
    <div className={style.wrapper}>
      {formData ? (
        <Formik initialValues={formData}>
          {() => (
            <form onChange={changeFormData}>
              <div className={style.profilesection}>
                <div className={style.profilephoto}>
                  <img src={avatar} alt="profile" />
                </div>
                <div className={style.profilebuttons}>
                  <ImageUploading onChange={onChange} dataURLKey="data_url">
                    {({
                      imageList,
                      onImageUpload,
                      onImageRemoveAll,
                      onImageUpdate,
                      onImageRemove,
                      isDragging,
                      dragProps,
                    }) => (
                      <div className={style.changepicture}>
                        <button
                          onClick={(e) => {
                            e.preventDefault();
                            onImageUpload(e);
                          }}
                          disabled={!isEdit || imgUploadLoading}
                        >
                          {imgUploadLoading ? (
                            <InlineLoader size={15} color="white" />
                          ) : (
                            "Change Picture"
                          )}
                        </button>
                      </div>
                    )}
                  </ImageUploading>

                  <div className={style.deletepicture}>
                    <button
                      onClick={handleDeleteImage}
                      disabled={!isEdit || imgUploadLoading}
                    >
                      Delete picture
                    </button>
                  </div>
                </div>
              </div>

              <div className={style.textFieldGroup}>
                <div className={style.row}>
                  <div className={style.column}>
                    <div className={style.field}>
                      <label htmlFor="first_name">First Name</label>
                      <TextField
                        className={style.textField}
                        type="text"
                        name="first_name"
                        value={formData.first_name}
                        placeholder="First Name"
                        disabled={!isEdit}
                      />
                    </div>
                  </div>
                  <div className={style.column}>
                    <div className={style.field}>
                      <label htmlFor="last_name">Last Name</label>
                      <TextField
                        className={style.textField}
                        type="text"
                        name="last_name"
                        value={formData.last_name}
                        placeholder="Last Name"
                        disabled={!isEdit}
                      />
                    </div>
                  </div>
                </div>

                <div className={style.field}>
                  <label htmlFor="email">Email ID</label>
                  <TextField
                    className={style.textField}
                    type="email"
                    name="email"
                    value={formData.email}
                    disabled
                  />
                </div>
                <div className={style.field}>
                  <label htmlFor="category">Category</label>
                  <TextField
                    className={style.textField}
                    type="text"
                    name="category"
                    value={formData.category}
                    disabled
                  />
                </div>
              </div>

              <div className={style.btnContainer}>
                {isEdit && (
                  <>
                    <button
                      className={style.editBtn}
                      onClick={handleCancel}
                      disabled={isLoading}
                    >
                      Cancel
                    </button>
                    <Button
                      submit
                      disabled={isLoading}
                      options={{
                        width: "175px",
                        height: "40px",
                        radius: "13px",
                      }}
                      onClick={handleSubmit}
                    >
                      {isLoading ? (
                        <InlineLoader size={20} color="white" />
                      ) : (
                        "Submit"
                      )}
                    </Button>
                  </>
                )}
                {!isEdit && (
                  <button className={style.editBtn} onClick={toggleIsEdit}>
                    Edit
                  </button>
                )}
              </div>
            </form>
          )}
        </Formik>
      ) : (
        <div className={style.loaderWrapper}>
          <InlineLoader />
        </div>
      )}
    </div>
  );
}
