import InlineLoader from "components/shared/InlineLoader";
import Modal from "components/shared/Modal";
import { useUserContext } from "contexts/UserContext";
import { useEffect, useState } from "react";
import {
  CircularProgressbar,
  buildStyles
} from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import { GoArrowUpRight } from "react-icons/go";
import { useNavigate } from "react-router-dom";
import { getRequest } from "utils/api";
import style from "./MyTest.module.scss";
import Popup from "./UpgradeModals/Popup";

function sortQuesCountByOrder(quesCount) {
  // Sort by order
  return quesCount.sort((a, b) => a.order - b.order);
}


const _Colors = {
  personality: "#6b7ff2",
  aptitude: "#f38585",
  interest: "#54c184",
  wvp: "#d24cde",
  eq: "#d24cde",
};

export default function MyTest() {
  const { state } = useUserContext();
  const [quesCount, setQuesCount] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [isAssessmentOver, setIsAssessmentOver] = useState(false);
  const [isModal, setModal] = useState(false);
  const [isPlanActive, setIsPlanActive] = useState(false);

  const handleCloseModal = () => {
    setModal(false);
  };

  function handleClick(item) {
    if (item.percentage === 100) {
      return navigateToReport();
    }

    if (!isPlanActive && item.name !== "Personality") {
      return setModal(true);
    }

    navigateToAssessment();
  }

  const navigate = useNavigate();

  function navigateToAssessment() {
    navigate("/assessment/live");
  }

  function navigateToReport() {
    navigate("/dashboard/candidate/reports");
  }

  function getAssessmentData() {
    setIsLoading(true);

    getRequest("/assessment/status/update")
      .then((resp) => {
        if (resp.data.is_assessment_finished) {
          setIsAssessmentOver(true);
        }
      })
      .catch((err) => {
        console.error(err);
      });

    getRequest(`/assessment/status`)
      .then((resp) => {
        const tabs = resp.data.data;

        setIsPlanActive(resp.data.has_active_plan);

        const sortedQuesCount = sortQuesCountByOrder(
          Object.values(tabs).map((tab, index) => ({
            order: tab.order,
            name: tab.title,
            key: index,
            count: tab.total_questions,
            total: tab.total_questions,
            status: tab.assessment_status,
            percentage: Math.round((tab.attempted / tab.total_questions) * 100),
            style: {
              backgroundColor: tab.color,
              width: Math.round((tab.attempted / tab.total_questions) * 100),
            },
          }))
        );

        setQuesCount(sortedQuesCount);

      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  useEffect(() => {
    if (state && state.profile) getAssessmentData();
  }, [state.profile]);

  return (
    <div className={style.wrapper}>
      <div className={style.card}>
        {isLoading && (
          <div className={style.loaderWrapper}>
            <InlineLoader size={80} />
          </div>
        )}

        {!isLoading && (
          <>
            <div className={style.bars}>
              {quesCount.map((item) => (
                <div
                  className={style.innercard}
                  onClick={() => handleClick(item)}
                >
                  <div className={style.status}>
                    <p>{item.status}</p>
                  </div>
                  <div className={style.barWrapper} key={item.name}>
                    <div className={style.caption}>{item.name}</div>

                    <div className={style.progressBarContainer}>
                      <div className={style.circular}>
                        <CircularProgressbar
                          value={item.percentage}
                          text={`${item.percentage}%`}
                          styles={buildStyles({
                            textColor: "black",
                            pathColor: item.style.backgroundColor,
                          })}
                        />
                      </div>
                    </div>
                  </div>
                  <div className={style.statusresult}>
                    <div
                      className={style.arrow}
                      style={{ color: item.style.backgroundColor }}
                    >
                      <GoArrowUpRight />
                    </div>
                    <div className={style.statusText}>
                      {item.percentage === 100 ? (
                        <p style={{ color: item.style.backgroundColor }}>
                          VIEW RESULT
                        </p>
                      ) : (
                        <p>CONTINUE ASSESSMENT</p>
                      )}
                    </div>
                    <div className={style.count}></div>
                  </div>
                </div>
              ))}
            </div>

            {!isAssessmentOver && (
              <div className={style.button}>
                <button onClick={navigateToAssessment}>Continue Assessment</button>
              </div>
            )}
          </>
        )}
      </div>

      <Modal isOpen={isModal} contentClassName={style.modal}>
        <Popup handleCloseModal={handleCloseModal} />
      </Modal>
    </div>
  );
}
