import style from "./EQ.module.scss";
import Locked from "./Locked";

function EQ({ isAssessmentCompleted, hasActivePlan, notCompleted, report }) {
  const eq = report?.dominant_eq || [];

  return (
    <div>
      {hasActivePlan ? (
        isAssessmentCompleted ? (
          <div className={style.card}>
            {eq.slice(0, 6).map((item, index) => (
              <div className={style.innercard} key={index}>
                {item[0]}
                <div>
                  <div className={style.icon}>
                    <img src={item[1]} alt="" width={40} />
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          notCompleted
        )
      ) : (
        <Locked />
      )}
    </div>
  );
}

export default EQ;
