import FormControl from "@material-ui/core/FormControl";
import InputBase from "@material-ui/core/InputBase";
import Select from "@material-ui/core/Select";
import { makeStyles } from "@material-ui/core/styles";
import Pagination from "@material-ui/lab/Pagination";
import InlineLoader from "components/shared/InlineLoader";
import moment from "moment";
import { useEffect, useState } from "react";
import { getRequest } from "utils/api";
import style from "./AssignedLicenses.module.scss";
import {
  EditOutlined,
  SearchOutlined,
  VisibilityOutlined,
} from "@material-ui/icons";
import { useNavigate } from "react-router-dom";
import { FaCopy } from "react-icons/fa";
import { toast } from "react-toastify";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  root: {
    "& > *": {
      marginTop: theme.spacing(2),
    },
  },
}));

const AssignedLicenses = () => {
  const classes = useStyles();
  const navigateTo = useNavigate();

  const [licenses, setLicenses] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const [search, setSearch] = useState("");
  const [limit, setLimit] = useState(10);
  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);

  const offset = (page - 1) * limit;

  async function fetchLicenses(limit, offset) {
    setIsLoading(true);
    try {
      const queryParams = { limit, offset, is_assigned: true };

      if (search !== "") {
        queryParams.search = search;
      }

      const res = await getRequest("/vendors/licences", {}, queryParams);

      const licenses = res.data.results.map((row) => ({
        id: row.id,
        name: ((row.first_name || "") + " " + (row.last_name || "")).trim(),
        email: row.email,
        group: row.group_name || null,
        category: row.dept,
        created: row.created,
        updated: row.updated,
        results: row.notes,
        access_key: row.access_key,
        access_secret: row.access_secret,
        status: row.is_assigned ? "ASSIGNED" : "NOT ASSIGNED",
      }));
      setLicenses(licenses);
      setCount(res.data.count);
    } catch {
    } finally {
      setIsLoading(false);
    }
  }

  useEffect(() => {
    fetchLicenses(limit, offset);
  }, [limit, offset]);

  return (
    <div className={style.wrapper}>
      <div className={style.tableWrapper}>
        <div className={style.header}>
          Assigned Licenses
          <div className={style.subheader}>
            Below is a table detailing all assigned licenses, including the
            Name, Email ID, Group, Category, Assigned Date, Assessment Status,
            and Results.
          </div>
        </div>
        <div className={style.all}>
          <div style={{ display: "flex", alignItems: "center" }}>
            <span className={style.show}>Show</span>
            <FormControl variant="outlined" className={classes.formControl}>
              <Select
                className={style.suball}
                native
                value={limit}
                onChange={(e) => setLimit(e.target.value)}
                inputProps={{
                  name: "age",
                  id: "outlined-age-native-simple",
                }}
              >
                <option>10</option>
                <option>50</option>
                <option>100</option>
              </Select>
            </FormControl>
          </div>
          <div className={style.search}>
            <InputBase
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  fetchLicenses(limit, offset);
                }
              }}
              className={style.input}
              placeholder="Search here"
            />
            <button
              type="submit"
              className={style.iconButton1}
              aria-label="search"
              onClick={() => fetchLicenses(limit, offset)}
            >
              <SearchOutlined />
            </button>
          </div>
        </div>
        {isLoading ? (
          <InlineLoader />
        ) : (
          <table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Access Key</th>
                <th>Group</th>
                <th>Category</th>
                <th>Assigned Date</th>
                <th>Status</th>
                <th>Actions</th>
              </tr>
            </thead>

            <tbody>
              {licenses.map((license) => (
                <tr key={license.id}>
                  <td>{license.name || "-"}</td>
                  <td>{license.access_key}</td>
                  <td>{license.group || "-"}</td>
                  <td>{license.category || "-"}</td>
                  <td>{moment(license.created).format("DD/MM/YY")}</td>
                  <td>{license.status}</td>
                  <td
                    style={{ display: "flex", justifyContent: "space-between", alignItems: 'center' }}
                  >
                    <FaCopy
                      onClick={() => {
                        const copyString = `Access Key: ${license.access_key}\nAccess Secret: ${license.access_secret}`;
                        navigator.clipboard.writeText(copyString);
                        toast.success(
                          "Access Key and Access Secret has been copied to clipboard!"
                        );
                      }}
                    />

                    <EditOutlined
                      onClick={() => navigateTo(`${license.id}/edit`)}
                    />

                    <VisibilityOutlined
                      onClick={() => navigateTo(`${license.access_key}`)}
                    />
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
        <div className={style.last}>
          {/* <div>
            <p className={style.lasttxt}>Showing 1 to 10 of 57 Entries</p>
          </div> */}
          <div className={classes.root}>
            <Pagination
              count={Math.ceil(count / limit)}
              disabled={isLoading}
              page={page}
              value={page}
              onChange={(_, val) => setPage(val)}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AssignedLicenses;
