import InlineLoader from "components/shared/InlineLoader";
import { format } from "date-fns";
import { createRef, useEffect, useState } from "react";
import { Edit2 } from "react-feather";
import { useNavigate, useSearchParams } from "react-router-dom";
import { toast } from "react-toastify";
import { getRequest, patchRequest } from "utils/api";
import { capitalizeWord } from "utils/helper";
import style from "./Licenses.module.scss";

export default function Licenses() {
  const navigateTo = useNavigate();

  const [isLoading, setIsLoading] = useState({ count: false, table: 0 });
  const [licenses, setLicenses] = useState([]);
  // console.log(licenses);
  const [editedNotes, setEditedNotes] = useState({});

  const [refs, setRefs] = useState([]);

  const [searchParams] = useSearchParams();
  const dept = searchParams.get("dept");

  async function getLicenses() {
    setIsLoading((prev) => ({ ...prev, table: true }));

    const params = {};

    if (dept && dept.length) {
      params.dept = dept.toUpperCase();
    }

    try {
      const res = await getRequest(
        "/vendors/licences/",
        {
          removeTrailingSlash: dept && dept.length ? true : false,
        },
        params,
      );

      setLicenses(res.data.map((item) => ({ ...item, isEditingNote: false })));
    } catch (e) {
      toast.error("Something went wrong! Please try again.");
      console.error(e);
    } finally {
      setIsLoading((prev) => ({ ...prev, table: false }));
    }
  }

  function toggleNoteEdit(licensePk, index) {
    let toUpdate = [...licenses];
    toUpdate.forEach((item) => {
      if (item.pk === licensePk) {
        item.isEditingNote = !item.isEditingNote;
      }
    });
    setLicenses(toUpdate);

    refs[index].current.disabled = false;
    refs[index].current.focus();
  }

  function getDeptWord(word) {
    return word
      .split(" ")
      .map((item) => capitalizeWord(item))
      .join(" ");
  }

  function capitalise(str) {
    return str
      .split("_")
      .map((word) => capitalizeWord(word))
      .join(" ");
  }

  function changeNotes(licensePk, e) {
    const { value } = e.target;
    setEditedNotes((prev) => ({ ...prev, [licensePk]: value }));
  }

  function submitNotes(licensePk, e) {
    e.preventDefault();
    patchRequest(`/vendors/licences/${licensePk}`, {
      notes: editedNotes[licensePk],
    })
      .then(() => {
        getLicenses();
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => { });
  }

  useEffect(() => {
    getLicenses();
  }, []);

  useEffect(() => {
    setRefs((elRefs) =>
      Array(licenses.length)
        .fill()
        .map((_, i) => elRefs[i] || createRef()),
    );
  }, [licenses.length]);

  return (
    <div className={style.wrapper}>
      <div className={style.licensesWrapper}>
        <div className={style.header}>
          Licenses Created {dept && dept.length ? `(${capitalise(dept)})` : ``}
        </div>
        {isLoading.table ? (
          <div className={style.loaderWrapper}>
            <InlineLoader />
          </div>
        ) : (
          <div className={style.tableWrapper}>
            <table>
              <thead>
                <tr>
                  <th>Department</th>
                  <th>Access Key</th>
                  <th>Access Secret</th>
                  <th>Last Login</th>
                  <th>Action</th>
                  <th>Notes</th>
                </tr>
              </thead>

              <tbody>
                {licenses.map((license, index) => (
                  <tr key={license.pk}>
                    <td>{getDeptWord(license.dept.toLowerCase())}</td>
                    <td>{license.access_key}</td>
                    <td>{license.access_secret}</td>
                    <td>
                      {format(new Date(license.last_login), "dd MMM, hh:mm")}
                    </td>
                    <td>
                      <button
                        onClick={() =>
                          navigateTo(
                            `/dashboard/vendor/AssignNew?licenseId=${license.id}`,
                          )
                        }
                      >
                        Assign
                      </button>
                    </td>
                    <td>
                      <span>
                        <form
                          onChange={changeNotes.bind(this, license.pk)}
                          onSubmit={submitNotes.bind(this, license.pk)}
                        >
                          <input
                            type="text"
                            placeholder={
                              license.notes ? license.notes : "No Notes"
                            }
                            ref={refs[index]}
                            disabled={true}
                          />
                        </form>

                        <span>
                          <Edit2
                            onClick={toggleNoteEdit.bind(
                              this,
                              license.pk,
                              index,
                            )}
                            size={15}
                          />
                        </span>
                      </span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
}
