import SettingsIcon from "assets/images/dashboard/cus.svg";
import profieIconhover from "assets/images/dashboard/ic-dashboard-hover.svg";
import SettingsIconhover from "assets/images/dashboard/ic_customerhover.svg";
import profieIcon from "assets/images/dashboard/ic_dashboard_not_active.svg";
import TestsIconhover from "assets/images/dashboard/ic_menus.svg";
import packageIconhover from "assets/images/dashboard/ic_order_hover.svg";
import packageIcon from "assets/images/dashboard/ic_order_not_active.svg";
import TestsIcon from "assets/images/dashboard/package.svg";
import { useLocation } from "react-router-dom";
import SidebarLink from "./SidebarLink";
import style from "./SidebarLinks.module.scss";

const _LINKS = [
  {
    text: " My Assessment",
    link: "/dashboard/candidate/assessment",
    icon: packageIcon,
    hovericon: packageIconhover,
  },
  {
    text: "My Profile",
    link: "/dashboard/candidate/profile",
    icon: profieIcon,
    hovericon: profieIconhover,
  },

  {
    text: "My Report",
    link: "/dashboard/candidate/reports",
    icon: TestsIcon,
    hovericon: TestsIconhover,
  },
  {
    text: "Settings",
    link: "/dashboard/candidate/settings",
    icon: SettingsIcon,
    hovericon: SettingsIconhover,
  },
];

export default function SidebarLinks({ isMobileSidebar, toggleSidebar }) {
  const location = useLocation();

  function isActive(link) {
    if (
      link === location.pathname ||
      (link == "/dashboard/candidate/profile" &&
        location.pathname == "/dashboard/candidate")
    )
      return true;
    return false;
  }

  return (
    <div className={style.wrapper}>
      {_LINKS.map((link) => (
        <SidebarLink
          toggleSidebar={toggleSidebar}
          isMobileSidebar={isMobileSidebar}
          isActive={isActive(link.link)}
          key={link.text}
          text={link.text}
          link={link.link}
          icon={isActive(link.link) ? link.hovericon : link.icon}
        />
      ))}
    </div>
  );
}
