import InlineLoader from "components/shared/InlineLoader";
import { useEffect, useState } from "react";
import style from "./PrintReport.module.scss";
import { toast } from "react-toastify";
import { CloudDownloadOutlined } from "@material-ui/icons";
import { format } from "date-fns";
import axios from "axios";
import config from "config/config";

export default function Print({ accessKey }) {
  const [loading, setLoading] = useState(true);

  const [hasPDF, setHasPDF] = useState(false);

  const [url, setUrl] = useState(null);

  async function fetchPDF() {
    setLoading(true);

    try {
      const token = localStorage.getItem("accessToken");
      const res = await axios.get(
        `${config.apiBaseUrl}/assessment/report/${accessKey}/pdf`,
        {
          responseType: "blob",
          headers: { Authorization: `Bearer ${token}` },
        }
      );

      if (!res.data) {
        setHasPDF(false);
        return;
      }

      setHasPDF(true);

      const pdfBlob = new Blob([res.data], { type: "application/pdf" });
      const url = URL.createObjectURL(pdfBlob);

      setUrl(url);
    } catch (err) {
      console.error(err);
      toast.error("Something went wrong! Please try again.");
    } finally {
      setLoading(false);
    }
  }

  function handleDownload() {
    if (!url) return;

    const link = document.createElement("a");
    link.href = url;
    link.setAttribute(
      "download",
      `Report_${format(new Date(), "dd-MM-yyyy_hhmmss")}.pdf`
    );
    document.body.appendChild(link);
    link.click();

    document.body.removeChild(link);
  }

  useEffect(() => {
    fetchPDF();
  }, []);

  return (
    <div className={style.wrapper}>
      {loading ? (
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            width: "100%",
            height: "500px",
          }}
        >
          <InlineLoader size={90} />
        </div>
      ) : hasPDF ? (
        <div className={style.refContainer}>
          <iframe src={url} width="100%" height="750" frameBorder="0">
            Browser not compatible.
          </iframe>
        </div>
      ) : (
        <div style={{ margin: "20px" }}>
          No report available for this candidate!
        </div>
      )}
    </div>
  );
}
