import clsx from "clsx";
import InlineLoader from "components/shared/InlineLoader";
import { forwardRef, useEffect, useState } from "react";
import { Download, Eye } from "react-feather";
import { useParams } from "react-router";
import { getRequest } from "utils/api";
import BarGraph from "./BarGraph";
import style from "./ViewLicense.module.scss";
import PrintReport from "./full-report/PrintReport";
import { _Colors } from "./full-report/common/helper";

function ViewLicense(props, ref) {
  const { accessKey } = useParams();

  const MainTabs = [
    { name: "View Full Report", isOpen: true, icon: <Eye /> },
    { name: "Download Full Report", isOpen: false, icon: <Download /> },
  ];
  const _Tabs = [
    { name: "Personality Traits", isOpen: true },
    { name: "Aptitude", isOpen: false },
    { name: "Interest Area", isOpen: false },
    { name: "Work Value Preferense", isOpen: false },
    { name: "EQ", isOpen: false },
  ];

  const [isLoading, setIsLoading] = useState(false);
  const [isAssessmentCompleted, setIsAssessmentCompleted] = useState(false);
  const [tabs, setTabs] = useState(_Tabs);
  const [maintabs, setmainTabs] = useState(MainTabs);

  const [activeTab, setActiveTab] = useState("Personality Traits");
  const [MainactiveTab, MainsetActiveTab] = useState(0);

  function handleClickTab(name, index) {
    setTabs((prev) => {
      let toUpdate = [...prev];
      toUpdate.forEach((tab) => (tab.isOpen = false));
      toUpdate[index].isOpen = true;
      return toUpdate;
    });
    setActiveTab(name);
  }

  function handleClickMainTab(index) {
    setmainTabs((prev) => {
      let toUpdate = [...prev];
      toUpdate.forEach((tab) => (tab.isOpen = false));
      toUpdate[index].isOpen = true;
      return toUpdate;
    });
    MainsetActiveTab(index);
  }

  const [report, setReport] = useState({});

  async function getReport() {
    setIsLoading(true);

    try {
      const statusRes = await getRequest(`/assessment/${accessKey}/status`);

      const { is_assessment_finished } = statusRes.data;

      if (is_assessment_finished) {
        setIsAssessmentCompleted(true);
        const reportRes = await getRequest(`/assessment/report/${accessKey}`);
        setReport(reportRes.data);
      }
    } catch (err) {
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  }

  const tabViews = {

    "Personality Traits": (
      <div className={style.row}>
        <div className={clsx(style.graph, style.personality)}>
          <BarGraph
            height={400}
            horizontal={true}
            title={"Personality"}
            data={{
              labels: report?.graph_data?.personality_labels,
              values: report?.graph_data?.personality_final_list,
            }}
            color={_Colors.personality}
          />
        </div>
      </div>
    ),
    Aptitude: (
      <div className={style.row}>
        <div className={clsx(style.graph, style.aptitude)}>
          {report.graph_data && (
            <BarGraph
              height={300}
              horizontal={true}
              title={"Aptitude"}
              data={{
                labels: report?.graph_data?.aptitude_labels,
                values: report?.graph_data?.aptitude_final_list,
              }}
              color={_Colors.aptitude}
            />
          )}
        </div>
      </div>
    ),
    "Interest Area": (
      <div className={style.row}>
        <div className={clsx(style.graph, style.career)}>
          {report.graph_data && (
            <BarGraph
              height={450}
              horizontal={true}
              title={"Interests"}
              data={{
                labels: report?.graph_data?.interest_labels,
                values: report?.graph_data?.interest_final_list,
              }}
              color={_Colors.career}
            />
          )}
        </div>
      </div>
    ),
    "Work Value Preferense": (
      <div className={style.row}>
        <div className={clsx(style.graph, style.career)}>
          {report.graph_data && (
            <BarGraph
              height={300}
              horizontal={true}
              title={"Work Value Preference"}
              data={{
                labels: report?.graph_data?.wvp_labels,
                values: report?.graph_data?.wvp_final_list,
              }}
              color={_Colors.wvp}
            />
          )}
        </div>
      </div>
    ),
    EQ: (
      <div className={style.row}>
        <div className={clsx(style.graph, style.career)}>
          {report.graph_data && (
            <BarGraph
              height={250}
              horizontal={true}
              title={"Emotional Quotient"}
              data={{
                labels: report?.graph_data?.eq_labels,
                values: report?.graph_data?.eq_final_list,
              }}
              color={_Colors.eq}
            />
          )}
        </div>
      </div>
    ),
  };

  useEffect(() => {
    getReport();
  }, []);

  return (
    <div ref={ref} {...props} className={style.wrapper}>
      {/* {isActiveFullReport ? (
        
        <PrintReport
          handleClosePrint={handleClosePrint}
          report={report}
          department={state.profile.department}
        />
      ) : ( */}
      <>
        <div className={style.header}>
          {/* <div className={style.title}>Assessment Report</div>
            {isAssessmentCompleted && !isLoading && (
              <button onClick={handleClickReport}>
              Full Report
              </button>
            )} */}
          <div className={style.maintab}>
            {maintabs.map((tab, index) => (
              <div
                className={clsx(style.tab, tab.isOpen && style.open)}
                onClick={() => handleClickMainTab(index)}
                key={tab.name}
              >
                {tab.name}
                {tab.icon}
              </div>
            ))}
          </div>
        </div>
        {MainactiveTab == 0 && (
          <div className={style.tabmain}>
            <div className={style.tabs}>
              {tabs.map((tab, index) => (
                <div
                  className={clsx(style.tab, tab.isOpen && style.open)}
                  onClick={() => handleClickTab(tab.name, index)}
                  key={tab.name}
                >
                  {tab.name}
                </div>
              ))}
            </div>

            <div className={style.section}>
              {isLoading ? (
                <div className={style.loaderWrapper}>
                  <InlineLoader size={90} />
                </div>
              ) : (
                <>
                  {isAssessmentCompleted && tabViews[activeTab]}

                  {!isAssessmentCompleted && (
                    <div className={style.notCompleteWrapper}>
                      <div className={style.title}>
                        Assessment Not Completed
                      </div>
                      <div>
                        Oops, the candidate has not completed the assessment
                        yet.
                      </div>
                    </div>
                  )}
                </>
              )}
            </div>
          </div>
        )}

        {MainactiveTab == 1 && <PrintReport accessKey={accessKey} />}
      </>
    </div>
  );
}

export default forwardRef(ViewLicense);
