import clsx from "clsx";
import InlineLoader from "components/shared/InlineLoader";
import { useUserContext } from "contexts/UserContext";
import { useEffect, useState } from "react";
import { getRequest } from "utils/api";
import Aptitudes from "./Components/Aptitudes";
import EQ from "./Components/EQ";
import InterestedArea from "./Components/InterestedArea";
import PersonalityTraits from "./Components/PersonalityTraits";
import WorkValuePreference from "./Components/WorkValuePreference";
import style from "./Profile.module.scss";
import { useNavigate } from "react-router-dom";

export default function Profile() {
  const UserContext = useUserContext();
  const navigateTo = useNavigate();
  const profile = UserContext.state.profile;
  const { hasActivePlan } = UserContext;

  const [report, setReport] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const [isAssessmentCompleted, setIsAssessmentCompleted] = useState(false);
  const [isPersonalityCompleted, setIsPersonalityCompleted] = useState(false);

  const [imgUploadLoading, setImgUploadLoading] = useState();

  const Tabs = [
    { key: "personality_traits", name: "Personality Traits" },
    { key: "aptitudes", name: "Aptitudes" },
    { key: "interest_areas", name: "Interest Areas" },
    { key: "wvp", name: "Work Value Preferences" },
    { key: "eq", name: "EQ" },
  ];

  const [activeTab, setActiveTab] = useState(Tabs[0]);

  function navigateToAssessment() {
    navigateTo("/assessment/live");
  }

  function handleClickTab(tabKey) {
    const selectedTab = Tabs.find((item) => item.key === tabKey);

    if (selectedTab) {
      setActiveTab(selectedTab);
    }
  }

  const AssessmentNotCompleted = () => (
    <div className={style.notCompleteWrapper}>
      <div className={style.title}>Complete Assessment</div>
      <div>
        Oops, you have not completed the assessment. Please take the assessment
        to view report
      </div>
      <button onClick={navigateToAssessment}>Take Assessment</button>
    </div>
  );

  function renderSection() {
    switch (activeTab.key) {
      case "personality_traits":
        return (
          <PersonalityTraits
            isAssessmentCompleted={isAssessmentCompleted}
            isPersonalityCompleted={isPersonalityCompleted}
            report={report}
            notCompleted={<AssessmentNotCompleted />}
          />
        );
      case "aptitudes":
        return (
          <Aptitudes
            isAssessmentCompleted={isAssessmentCompleted}
            report={report}
            hasActivePlan={hasActivePlan}
            notCompleted={<AssessmentNotCompleted />}
          />
        );
      case "interest_areas":
        return (
          <InterestedArea
            isAssessmentCompleted={isAssessmentCompleted}
            report={report}
            hasActivePlan={hasActivePlan}
            notCompleted={<AssessmentNotCompleted />}
          />
        );
      case "wvp":
        return (
          <WorkValuePreference
            isAssessmentCompleted={isAssessmentCompleted}
            report={report}
            hasActivePlan={hasActivePlan}
            notCompleted={<AssessmentNotCompleted />}
          />
        );
      case "eq":
        return (
          <EQ
            isAssessmentCompleted={isAssessmentCompleted}
            report={report}
            hasActivePlan={hasActivePlan}
            notCompleted={<AssessmentNotCompleted />}
          />
        );
      default:
        return null;
    }
  }

  function getDeptName(dept) {
    let arrWords = dept.split("_");
    arrWords = arrWords.map((word) => capitalise(word));
    return arrWords.join(" ");
  }

  function capitalise(word) {
    if (!word) return "";
    let newWord = "";
    newWord += word.charAt(0).toUpperCase();
    for (let i = 1; i < word.length; i++) {
      newWord += word.charAt(i).toLowerCase();
    }
    return newWord;
  }

  async function fetchReport() {
    setIsLoading(true);

    try {
      const statusRes = await getRequest(`/assessment/status`);

      const { is_assessment_finished, is_personality_finished } =
        statusRes.data;

      setIsPersonalityCompleted(is_personality_finished);

      if (!hasActivePlan) {
        const response = await getRequest(`/assessment/basic/report`);

        setReport(response.data);
        setIsAssessmentCompleted(false);
      } else if (is_assessment_finished) {
        const response = await getRequest(`/assessment/report`);

        setReport(response.data);
        setIsAssessmentCompleted(true);
      } else {
        setIsAssessmentCompleted(false);
        setIsLoading(false);
      }
    } catch (err) {
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  }

  const languages = {
    EN: "English",
  };

  useEffect(() => {
    fetchReport();
  }, []);

  return (
    <div className={style.wrapper}>
      <div className={style.banner}></div>
      <div className={style.main}>
        <div className={style.left}>
          <div className={style.header}>
            <div
              className={clsx(style.image, imgUploadLoading && style.border)}
            >
              {imgUploadLoading ? (
                <InlineLoader />
              ) : (
                <img
                  src={profile.photo}
                  alt="profile"
                  className={style.image}
                />
              )}

              {/* {!imgUploadLoading && (
                <ImageUploading onChange={onChange} dataURLKey="data_url">
                  {({
                    imageList,
                    onImageUpload,
                    onImageRemoveAll,
                    onImageUpdate,
                    onImageRemove,
                    isDragging,
                    dragProps,
                  }) => (
                    <div onClick={onImageUpload} className={style.editIcon}>
                      <Edit2 size={13} />
                    </div>
                  )}
                </ImageUploading>
              )} */}
            </div>
            <div className={style.name}>{profile.fullname}</div>
          </div>
          {profile.department && (
            <div className={style.body}>
              {[
                { name: "Email Id", value: profile.email },
                { name: "Category:", value: getDeptName(profile.department) },
                { name: "Language:", value: languages[profile.pref_language] },
              ].map((item) => (
                <div className={style.field} key={item.name}>
                  <div>{item.name}</div>
                  <div>{item.value}</div>
                </div>
              ))}
            </div>
          )}
        </div>
        <div className={style.right}>
          {isLoading ? (
            <div
              style={{
                width: "100%",
                height: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <InlineLoader size={50} />
            </div>
          ) : (
            <>
              <div className={style.tabs}>
                {Tabs.map((tab) => (
                  <div
                    className={clsx(
                      style.tab,
                      tab.key === activeTab.key && style.open
                    )}
                    onClick={handleClickTab.bind(this, tab.key)}
                    key={tab.key}
                  >
                    {tab.name}
                  </div>
                ))}
              </div>
              <div className={style.section}>{renderSection()}</div>
            </>
          )}
        </div>
      </div>
    </div>
  );
}
