import InlineLoader from "components/shared/InlineLoader";
import { Field, Formik } from "formik";
import { useState } from "react";
import { toast } from "react-toastify";
import { putRequest } from "utils/api";
import style from "./customizeLanding/CustomizeLanding.module.scss";

export default function ChangePassword() {
  const [formData, setFormData] = useState({
    old_password: "",
    password: "",
    password2: "",
  });
  const [isLoading, setIsLoading] = useState(false);

  function handleFormChange(e) {
    const { name, value } = e.target;

    setFormData((prev) => ({ ...prev, [name]: value }));
  }

  async function handleFormSubmit() {
    const { old_password, password, password2 } = formData;

    if (!old_password || !password || !password2) {
      toast.error("Please enter your current and new password!");
      return;
    }

    if (password !== password2) {
      toast.error("Passwords do not match!");
      return;
    }

    setIsLoading(true);

    try {
      await putRequest("/accounts/update_password", {
        old_password,
        password,
        password2,
      });

      toast.success("Password Changed Successfully!");
      setFormData({
        old_password: "",
        password: "",
        password2: "",
      });
    } catch (e) {
      const errorMessages = [];
      if (e.response) {
        const { old_password, password } = e.response.data;

        if (old_password && old_password.old_password) {
          errorMessages.push(old_password.old_password);
        }
        if (password.length > 0) {
          errorMessages.push(...password);
        }
      }

      if (errorMessages.length > 0) {
        for (const item of errorMessages) {
          toast.error(item);
        }
      } else {
        toast.error("Something went wrong! Please try again.");
      }

      console.error(e);
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <div className={style.wrapper}>
      <div className={style.inner}>
        <div className={style.left}>
          <div className={style.lefthead}>
            <p className={style.mainhead}>Change Your Password</p>
          </div>
          <Formik initialValues={formData} onSubmit={handleFormSubmit}>
            {({ handleSubmit }) => (
              <form onChange={handleFormChange} onSubmit={handleSubmit}>
                <div className={style.textFieldGroup}>
                  <div className={style.row}>
                    <div className={style.column}>
                      <div className={style.field}>
                        <label htmlFor="old_password">Current Password</label>
                        <Field
                          name="old_password"
                          className={style.textField}
                          type="password"
                          value={formData.old_password}
                          placeholder="Enter your current password..."
                          disabled={isLoading}
                        />
                      </div>
                    </div>
                  </div>
                  <div className={style.row} style={{ marginTop: "15px" }}>
                    <div className={style.column}>
                      <div className={style.field}>
                        <label htmlFor="password">Enter New Password</label>
                        <Field
                          name="password"
                          className={style.textField}
                          type="password"
                          value={formData.password}
                          placeholder="Enter new password..."
                          disabled={isLoading}
                        />
                      </div>
                    </div>
                  </div>
                  <div className={style.row} style={{ marginTop: "15px" }}>
                    <div className={style.column}>
                      <div className={style.field}>
                        <label htmlFor="password2">Re-enter New Password</label>
                        <Field
                          name="password2"
                          className={style.textField}
                          type="password"
                          value={formData.password2}
                          placeholder="Re-enter your new password..."
                          disabled={isLoading}
                        />
                      </div>
                    </div>
                  </div>
                </div>

                <div className={style.btnContainer}>
                  <button submit disabled={isLoading}>
                    {isLoading ? (
                      <InlineLoader size={20} color="white" />
                    ) : (
                      "Submit"
                    )}
                  </button>
                </div>
              </form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
}
