import clsx from "clsx";
import { useEffect, useState } from "react";
import Accountsettings from "./AccountSetting";
import ChangePassword from "./ChangePassword";
import Myplan from "./MyPlan";
import style from "./Settings.module.scss";
import { useSearchParams } from "react-router-dom";

function SettingsContainer() {
  const [searchParams] = useSearchParams();
  const tabKey = searchParams.get("tabKey");

  const tabs = [
    { key: "account_settings", name: "Account Settings" },
    { key: "change_password", name: "Change Password" },
    { key: "my_plan", name: "My Plan" },
    ,
  ];

  const [activeTab, setActiveTab] = useState(tabs[0]);

  function handleClickTab(tabKey) {
    const selected = tabs.find((item) => item.key === tabKey);
    setActiveTab(selected);
  }

  function renderSection() {
    switch (activeTab.key) {
      case "account_settings":
        return <Accountsettings />;
      case "change_password":
        return <ChangePassword />;
      // case "Notifications":
      //   return <Notifications />;
      case "my_plan":
        return <Myplan />;
      default:
        return null;
    }
  }

  useEffect(() => {
    if (tabKey && tabKey !== "") {
      const selectedTab = tabs.find((item) => item.key === tabKey);

      if (selectedTab) {
        setActiveTab(selectedTab);
      } else {
        searchParams.set("tabKey", activeTab.key);
      }
    }
  }, [tabKey]);

  return (
    <div className={style.wrapper}>
      <div className={style.tabs}>
        {tabs.map((tab) => (
          <div
            className={clsx(style.tab, tab.key === activeTab.key && style.open)}
            onClick={handleClickTab.bind(this, tab.key)}
            key={tab.key}
          >
            {tab.name}
          </div>
        ))}
      </div>
      <div className={style.section}>{renderSection()}</div>
    </div>
  );
}

export default SettingsContainer;
