import MenuItem from "@material-ui/core/MenuItem";
import Popover from "@material-ui/core/Popover";
import AvatarImg from "assets/images/dashboard/user_avatar.png";
import LogoImg from "assets/images/main/02.png.svg";
import clsx from "clsx";
import { useSidebarContext, useUserContext } from "contexts/AllContexts";
import { useEffect, useState } from "react";
import { Menu } from "react-feather";
import { useNavigate } from "react-router-dom";
import { getRequest } from "utils/api";
import style from "./DashNav.module.scss";

export default function DashNav() {
  const SidebarContext = useSidebarContext();
  const UserContext = useUserContext();

  const sidebarIsOpen = SidebarContext.state.isOpen;
  const sidebarIsMobile = SidebarContext.state.isMobile;
  const profile = UserContext.state.profile;

  const navigate = useNavigate();
  const [vendorProfile, setVendorProfile] = useState({});

  function handleLogout() {
    localStorage.removeItem("accessToken");
    sessionStorage.removeItem("accessToken");
    window.location.href = "/auth/login";
  }

  function toggleSidebar() {
    SidebarContext.dispatch({ type: "TOGGLE_SIDEBAR" });
  }

  function handleClickLogo() {
    navigate("/");
  }
  function handleGenLicense() {
    navigate("/dashboard/vendor/license/new");
  }

  function handleVendorType() {
    getRequest(`/vendors/profile`)
      .then((resp) => {
        setVendorProfile(resp.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  useEffect(() => {
    handleVendorType();
  }, []);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const handlelog = () => {
    setAnchorEl(null);

    localStorage.removeItem("accessToken");
    sessionStorage.removeItem("accessToken");
    window.location.href = "/auth/login";
  };

  const profileImage = vendorProfile.photo ? vendorProfile.photo : AvatarImg;

  return (
    <div className={clsx(style.wrapper)}>
      <div className={style.logo} onClick={handleClickLogo}>
        <img src={LogoImg} alt="" />
      </div>

      <button className={clsx(style.ham, sidebarIsMobile && style.mobile)} onClick={toggleSidebar}>
        <Menu size={15} /> {sidebarIsOpen ? "Hide" : "Show"} Menu
      </button>

      <div className={style.box}>
        <button onClick={handleGenLicense}>Generate License</button>

        <div onClick={handleMenu} className={style.name}>
          <span>{profile.fullname}</span>
          <div className={style.imgWrap}>
            <img src={profile.photo} alt="" />
          </div>
        </div>

        <Popover
          id="menu-appbar"
          anchorEl={anchorEl}
          keepMounted
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "center",
          }}
          open={open}
          onClose={handleClose}
        >
          <MenuItem
            style={{ padding: "18px", fontFamily: "Outfit", fontSize: "13px", borderBottom: "1px solid #E5E5E5" }}
            onClick={() => navigate("/dashboard/vendor/change-password")}
          >
            Change Password
          </MenuItem>
          <MenuItem
            style={{ padding: "18px", fontFamily: "Outfit", fontSize: "13px", borderBottom: "1px solid #E5E5E5" }}
            onClick={handlelog}
          >
            Logout
          </MenuItem>
        </Popover>
      </div>
    </div >
  );
}
