import {
  default as CareerTestLogo,
  default as LogoImg,
} from "assets/images/main/02.png.svg";
import clsx from "clsx";
import InlineLoader from "components/shared/InlineLoader";
import { useUserContext } from "contexts/UserContext";
import { Field, Formik } from "formik";
import { useEffect, useState } from "react";
import ImageUploading from "react-images-uploading";
import { toast } from "react-toastify";
import { getRequest, patchRequest } from "utils/api";
import style from "./CustomizeLanding.module.scss";
import Firstpage from "./component/Firstpage";

const CustomizeLanding = () => {
  const [formData, setFormData] = useState({
    page_heading: "",
    page_description: "",
  });

  const MainTabs = [
    { name: "firstpage", isOpen: true, img: LogoImg },
    { name: "CareerPedia", isOpen: false },
    { name: "About CareerTest.ai", isOpen: false },
  ];
  const [isLoading, setIsLoading] = useState(false);
  const [imgUploadLoading, setImgUploadLoading] = useState();
  const [maintabs, setmainTabs] = useState(MainTabs);
  const [isEdit, setIsEdit] = useState(false);
  const [slug, setSlug] = useState("");

  const [activeTab, setActiveTab] = useState(0);

  const userContext = useUserContext();

  async function fetchDetails() {
    try {
      const res = await getRequest("/vendors/page/build/");

      const { page_heading, page_description, slug } = res.data;

      setSlug(slug);
      setFormData({ page_heading, page_description });
    } catch (e) {
      toast.error("Something went wrong!");
    }
  }

  function handleClickMainTab(index) {
    setmainTabs((prev) => {
      let toUpdate = [...prev];
      toUpdate.forEach((tab) => (tab.isOpen = false));
      toUpdate[index].isOpen = true;
      return toUpdate;
    });
    setActiveTab(index);
  }

  const tabs = [
    <Firstpage
      heading={formData.page_heading}
      description={formData.page_description}
      src={userContext?.vendorProfile?.page_logo || CareerTestLogo}
    />,
  ];

  async function onImageChange(imageList) {
    setImgUploadLoading(true);
    const image = imageList[0].file;
    const formData = new FormData();
    formData.append("page_logo", image);

    setImgUploadLoading(true);
    try {
      await patchRequest("/vendors/page/build/", formData);

      toast.success("Image Upload Success!");
      userContext.getProfile();
    } catch (e) {
      console.error(e);
      toast.error("Something went wrong!");
    } finally {
      setImgUploadLoading(false);
    }
  }

  async function handleDeletePhoto(e) {
    e.preventDefault();

    const formData = new FormData();
    formData.append("photo", null);

    try {
      await patchRequest("/vendors/page/build/", { page_logo: null });

      toast.success("Image Removed!");
      userContext.getProfile();
    } catch (e) {
      console.error(e);
      toast.error("Something went wrong!");
    } finally {
    }
  }

  function handleFormChange(e) {
    const { name, value } = e.target;

    setFormData((prev) => ({ ...prev, [name]: value }));
  }

  async function handleFormSubmit() {
    const { page_heading, page_description } = formData;
    if (!page_heading || !page_description) {
      toast.error("Please enter heading and description!");
      return;
    }

    setIsLoading(true);

    try {
      await patchRequest("/vendors/page/build", formData);

      setIsEdit(false);
      fetchDetails();

      toast.success("Update success!");
    } catch (e) {
      toast.error("Something went wrong! Please try again...");
    } finally {
      setIsLoading(false);
    }
  }

  useEffect(() => {
    fetchDetails();
  }, []);

  return (
    <div className={style.wrapper}>
      <div className={style.inner}>
        <div className={style.left}>
          <div className={style.lefthead}>
            <p className={style.mainhead}>Customize Your Landing Page</p>
            <p className={style.subtext}>
              Personalize your landing page here to align with your
              organization's branding.
            </p>
          </div>
          <Formik initialValues={formData} onSubmit={handleFormSubmit}>
            {({ handleSubmit }) => (
              <form onChange={handleFormChange} onSubmit={handleSubmit}>
                <div className={style.profilesection}>
                  <div className={style.profilephoto}>
                    <img
                      src={
                        userContext?.vendorProfile?.page_logo || CareerTestLogo
                      }
                      alt="profile"
                    />
                  </div>
                  <div className={style.profilebuttons}>
                    <ImageUploading
                      onChange={onImageChange}
                      dataURLKey="data_url"
                    >
                      {({ onImageUpload }) => (
                        <div
                          className={style.changepicture}
                          onClick={(e) => {
                            e.preventDefault();
                            onImageUpload(e);
                          }}
                          disabled={imgUploadLoading}
                        >
                          <button disabled={imgUploadLoading}>
                            {imgUploadLoading ? (
                              <InlineLoader size={20} />
                            ) : (
                              "Change Picture"
                            )}
                          </button>
                        </div>
                      )}
                    </ImageUploading>

                    <div className={style.deletepicture}>
                      <button
                        onClick={handleDeletePhoto}
                        disabled={
                          imgUploadLoading ||
                          !userContext?.vendorProfile?.page_logo
                        }
                      >
                        Delete Picture
                      </button>
                    </div>
                  </div>
                </div>

                <div className={style.profilesection}>
                  Open your custom landing page{" "}
                  <a
                    href={`https://careertest.ai/auth/${slug}/login`}
                    target="_blank"
                  >
                    here
                  </a>
                </div>

                <div className={style.textFieldGroup}>
                  <div className={style.row}>
                    <div className={style.column}>
                      <div className={style.field}>
                        <label htmlFor="page_heading">Heading</label>
                        <Field
                          name="page_heading"
                          className={style.textField}
                          type="text"
                          value={formData.page_heading}
                          placeholder="Enter your heading here..."
                          disabled={!isEdit}
                        />
                      </div>
                    </div>
                  </div>

                  <div className={style.field1}>
                    <label htmlFor="page_description">Message</label>
                    <Field
                      as="textarea"
                      name="page_description"
                      placeholder="Enter your message here..."
                      className={style.textarea}
                      rows={20}
                      maxlength={200}
                      value={formData.page_description}
                      disabled={!isEdit}
                    />
                  </div>
                  <div className={style.wordCounter}>
                    {formData.page_description?.length || 0}/200 words
                  </div>
                </div>

                <div className={style.btnContainer}>
                  {isEdit ? (
                    <>
                      <button
                        disabled={isLoading}
                        onClick={(e) => {
                          e.preventDefault();
                          setIsEdit(false);
                        }}
                      >
                        Cancel
                      </button>
                      <button submit disabled={isLoading}>
                        {isLoading ? (
                          <InlineLoader size={20} color="white" />
                        ) : (
                          "Submit"
                        )}
                      </button>
                    </>
                  ) : (
                    <button
                      disabled={isLoading}
                      onClick={(e) => {
                        e.preventDefault();
                        setIsEdit(true);
                      }}
                    >
                      Edit
                    </button>
                  )}
                </div>
              </form>
            )}
          </Formik>
        </div>

        <div className={style.right}>
          <div className={style.maintab}>
            <div className={style.preview}>Preview</div>
            {maintabs.map((tab, index) => (
              <div className={clsx(style.tab, tab.isOpen && style.open)}>
                {tab.img ? (
                  <img src={LogoImg} className={style.logo} alt="" />
                ) : (
                  tab.name
                )}
              </div>
            ))}
          </div>

          <div className={style.section}>{tabs[activeTab]}</div>
        </div>
      </div>
    </div>
  );
};

export default CustomizeLanding;
